@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
:root{--light-green: #2dee7b;--black: #000;--white: #fff;--light-gray: #afafaf;--gray: #333;--dark-gray: #1d1d1d;--dark-gray-opacity-90: rgba(29, 29, 29, 0.9)}*{font-family:"Nunito", sans-serif}body,ul,li{margin:0;padding:0}li{list-style:none}::-webkit-scrollbar-track{background-color:transparent}::-webkit-scrollbar{width:3px;height:5px;border-radius:5px}::-webkit-scrollbar-thumb,::-webkit-scrollbar-corner{background:#1d1d1d;background:var(--dark-gray);border-radius:5px;width:5px}

.container_root{max-width:100vw;min-height:100vh;background-color:var(--black);position:relative;display:flex;flex-direction:column;align-items:end;justify-content:space-around;padding:20px 0}.about,.seasons{width:100vw;height:auto;position:relative}.title{width:100vw;height:100px;position:relative;z-index:11}@media screen and (max-width: 425px){.title{width:100vw;text-align:center;padding:0}}


.tabs__seasons{width:250px;height:auto;background-color:transparent;overflow-y:auto;padding:20px;margin:auto;z-index:5;position:relative}.tabs__btns{width:100%;height:50px;border-bottom:1px solid var(--light-gray)}.tab__btn{width:auto;height:100%;padding:10px;border:none;cursor:pointer;background-color:transparent;color:var(--white);font-weight:bold;outline:transparent}.tab__active{border-bottom:2px solid var(--light-green)}@media screen and (min-width: 425px){.tabs__seasons{width:300px}}@media screen and (min-width: 1024px){.tabs__seasons{width:400px}}

.season_btn_epsode{background-color:transparent;color:var(--white);cursor:pointer;height:50px;width:100%;border:none;text-align:left;outline:none;font-size:15px;display:flex;align-items:center;justify-content:space-between}.epsode_content{height:0;opacity:0;transition:all 0.4s ease-out;position:relative}.epsode_content .container_epsode_img,.epsode_content .container_epsode_synopsis{width:90%;margin:auto}.epsode_content .container_epsode_img .epsode_img,.epsode_content .container_epsode_synopsis .epsode_img{width:100%;background-size:contain}.epsode_content .container_epsode_synopsis .epsode_synopsis{color:var(--white);font-size:12px}.epsode_content.show{height:250px;opacity:1}.epsodes{padding:0 10px;overflow:hidden;background-color:transparent;transition:all 0.5s ease-in-out;border-bottom:1px solid var(--light-gray)}.icon_play{width:25px;height:25px}.epsode_content_btn_close{width:30px;height:20px;cursor:pointer;border:none;background-color:transparent;position:absolute;right:15px;top:5px;outline:none}.epsode_content_btn_close .icon_close{width:10px;height:10px}@media screen and (min-width: 768px){.epsode_content_btn_close{right:15px;top:5px}.epsode_content.show{height:300px}.epsode_content .container_epsode_synopsis .epsode_synopsis{font-size:14px}}@media screen and (min-width: 1024px){.epsode_content.show{height:300px}}

.bar{width:100%;height:2px;margin:auto;background-color:var(--gray)}.progress{background-color:var(--light-green);height:2px}


.tabs__general{width:90%;height:400px;background-color:var(--dark-gray);padding:15px;position:relative;z-index:10;margin:auto}@media screen and (min-width: 768px){.tabs__general{height:300px}}

.cast{width:90%;height:200px;margin:auto;font-size:14px;text-align:justify}.cast__list{width:100%;height:100%;overflow-x:auto;display:flex;align-items:center}.cast__item{width:150px;height:75px;background-color:var(--gray);border-radius:3px;margin:0 5px;color:var(--white);display:flex;align-items:center;justify-content:center}

.container_general{padding:15px}.container_icons{padding:10px}.icons_list{display:flex;align-items:center;justify-content:space-around}.icon_item{width:60px;height:60px}.container_synopsis{width:90%;margin:auto;font-size:14px;text-align:justify;line-height:30px}.container_synopsis h3{text-transform:uppercase;color:var(--white);font-weight:500;font-size:18px}.synopsis{color:var(--white);font-weight:400}.icon_btn{border:none;background-color:transparent;cursor:pointer;width:100%;height:100%;padding:0;outline:none}.icon_btn figcaption{color:var(--light-gray)}@media screen and (min-width: 768px){.container_general{height:200px;padding:15px;display:flex;flex-direction:row-reverse;align-items:center;justify-content:space-between}.container_icons{width:50%}.icons_list{display:flex;align-items:center;justify-content:space-around}}@media screen and (min-width: 1023px){.container_synopsis{width:50%;margin:0}.container_general{height:200px;padding:15px;display:flex;flex-direction:row-reverse;align-items:center;justify-content:space-around}.icons_list{display:flex;align-items:center;justify-content:space-around}}

.font--size{margin:0;font-size:60px;color:var(--white);padding-left:60px}@media screen and (max-width: 425px){.font--size{margin:0;font-size:35px;color:var(--white);padding:0}}

.container__img{width:100%;height:600px;position:absolute;z-index:1;top:0}.img{width:100%;height:100%;object-fit:cover}.background-alpha{width:100%;height:100%;background:linear-gradient(0deg, rgba(0,0,0,0.99) 0%, rgba(0,0,0,0.9) 38.54%, rgba(0,0,0,0.87) 67.71%, rgba(0,0,0,0.63) 85.94%, rgba(0,0,0,0.45) 100%);position:absolute;top:0}@media screen and (min-width: 1024px){.container__img{height:750px}}@media screen and (min-width: 1440px){.container__img{height:900px}}

